/* Fonts */
/* Header/Nav/Footer */
/* font-family: 'Heebo', sans-serif */
/* Main Body */
/* font-family: 'Hind', sans-serif */

main {
  margin: 0 auto;
  max-width: 90%;
}

:root {
  --primary: #1a1423;
  --secondy: #372549;
  --tertiary: #774c60;
  --accent: #b75d69;
}

h1,
h2,
h3 {
  font-family: "Heebo", sans-serif;
  margin: 5px 0;
  color: var(--secondy);
}

p,
span,
a {
  font-family: "Hind", sans-serif;
  color: var(--tertiary);
}

body {
  background-color: #eacdc2;
  text-align: center;
}

.name-header {
  font-size: 3.5rem;
  text-decoration: none;
}

.navbar {
  display: block;
  width: 100%;
  background-color: #cc8b86;
  top: 0;
}

.navtabs {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  justify-content: space-evenly;
}

.cats {
  display: inline-block;
}

#about {
  font-family: "Hind", sans-serif;
  color: var(--primary);
  font-size: 40px;
}

.portrait {
  clip-path: circle();
  width: 250px;
}

#aboutMe {
  margin: 0 auto;
  max-width: 40%;
}

li {
  list-style: none;
}

nav li {
  cursor: pointer;
}

nav a:hover {
  text-decoration: none;
}
